<template>
  <transition name="slide">
    <div>
        <div class="head">
            <img src="static/doctor.jpg" class="doctorImg">
            <div class="doctorInfo">
                <div class="honourBack">
                    <span class="doctorHonour">朱氏头皮针</span>
                </div> 
                <div>
                    <span>朱明清</span>
                    <span class="doctorPosition">教授</span>
                </div>
                <div>
                    <span class="verbCost">￥200</span>
                </div>
            </div>
        </div>
        <div class="introduce">
            <div class="introduceLeft">
                <img src="static/verb.png" class="verbImg">
            </div>
            <div class="introduceRight">
                <pre v-bind:class="{ preHeight: foldOpen }">美籍华人，美国朱氏头皮针医学教育基金会董事长。
    【朱氏头皮针】创始人：
    “朱氏针灸神经医学中心”首席顾问及主任医师；
    “朱氏头皮针研究暨教育基金会”主席；
    南京中医药大学兼职教授，研究生导师；
    美国俄勒冈州波特兰中医大学博士生导师；
    香港港九中医师公会永远荣誉会长。</pre>
            </div>
        </div>
        <div class="fold" @click="foldClick">
            <span v-show="foldOpen">...</span>
            <img :src="foldImgUrl">
        </div>
        <div class="verbDateBoxOut">
            <div class="verbDateBox" ref="verbDateBox">
                <div class="verbDate" v-for="(item, index) in dateList" :key="index" ref="verbDate">
                    <span>{{item.date}}</span>
                    <span>{{item.day}}</span>
                    <div v-bind:class="{ noVerbDate: !item.status, hasVerbDate:  item.status }">
                        <span v-if="item.status">预约</span>
                        <span v-if="!item.status">约满</span>
                    </div>
                </div>
            </div>
        </div>
        <div class="block">
            <block-head :title="'预约人'"></block-head>
            <div class="verbContent">
                <div class="verbContentLeft">
                    <div class="formItem">
                        <span class="itemTitle">姓名</span>
                        <input class="itemInput" placeholder="请输入姓名"></input>
                    </div>
                    <div class="formItem">
                        <span class="itemTitle">性别</span>
                        <input class="itemRadio" type="radio" name="sex"><label class="itemLabel">男</label></input>
                        <input class="itemRadio" type="radio" name="sex"><label class="itemLabel">女</label></input>
                    </div>
                    <div class="formItem">
                        <span class="itemTitle">年龄</span>
                        <input class="itemInput" placeholder="请输入年龄"></input>
                    </div>
                    <div class="formItem">
                        <span class="itemTitle">手机号码</span>
                        <input class="itemInput" placeholder="请输入手机号码"></input>
                    </div>
                </div>
                <div class="verbContentRight">
                    <img src="static/userInfo.png">
                </div>
            </div>
        </div>
        <div class="block">
            <block-head :title="'朱氏头皮针特色'"></block-head>
            <div>
                <pre>1、通过头部特定的穴位和适量的刺激来激发经络本身的功能，达到疏通经络调理血气的作用。
2、依据中国传统医统理论为指引，脏腑经络学为基础，西医颅部解剖及神经学为用，一同发挥。
3、在头部用浅刺、透穴刺治疗全身的病。
4、采独创的抽气发、进气法为运针手法。（各式头皮针皆不采提插发）
5、配合吐纳、导引、引气至病所。
6、不受体位影响，患者可自由活动患部，减少心理障碍，增进疗效。
7、治疗危、急、重、瘫、痛诸症更见效。
                </pre>
            </div>
        </div>
        <div class="verbBox">
            <div class="verbBtn" @click="goCheckOrder">
                <span>提交</span>
            </div>
        </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">

import blockHead from 'components/blockHead/blockHead'

export default {
  name: 'doctorVerb',
  created: function () {
    console.log('页面开始加载')
    var date = new Date()
    for (var i = 0; i < 7; i++) {
      var addDate = new Date((date / 1000 + 86400 * i) * 1000)
      var str = addDate.getMonth() + '-' + addDate.getDate()
      var day
      var status
      if (addDate.getDay() === 1) {
        day = '周一'
        status = 1
      } else if (addDate.getDay() === 2) {
        day = '周二'
        status = 0
      } else if (addDate.getDay() === 3) {
        day = '周三'
        status = 1
      } else if (addDate.getDay() === 4) {
        day = '周四'
        status = 1
      } else if (addDate.getDay() === 5) {
        day = '周五'
        status = 1
      } else if (addDate.getDay() === 6) {
        day = '周六'
        status = 1
      } else if (addDate.getDay() === 0) {
        day = '周日'
        status = 0
      }
      this.dateList.push({date: str, day: day, status: status})
    }
  },
  mounted () {
    var verbDataWidth = `${document.documentElement.clientWidth}` / 5
    this.$refs.verbDateBox.style.width = verbDataWidth * 7 + 'px'
  },
  data () {
    return {
      foldImgUrl: 'static/down.png',
      foldOpen: true,
      dateList: []
    }
  },
  methods: {
    foldClick: function () {
      this.foldOpen = !this.foldOpen
      if (this.foldOpen) {
        this.foldImgUrl = 'static/down.png'
      } else {
        this.foldImgUrl = 'static/up.png'
      }
    },
    goCheckOrder: function () {
      this.$router.push({
        path: `/checkOrder`
      })
    }
  },
  components: {
    blockHead
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus" scoped>
@import "~common/stylus/variable"
@import "~common/stylus/base"

$img_height_doctor = 80px
.doctorImg
    width $img_height_doctor
    height $img_height_doctor
    border-radius $img_height_doctor/2
    border 2px solid $color-theme-blue
    margin-left 20px

.head
    @extend .flex
    @extend .flex-row
    @extend .flex-start
    height $img_height_doctor + 40
    background-color white

.doctorInfo
    @extend .flex
    @extend .flex-column
    justify-content space-around
    text-align left
    margin-left 20px
    height $img_height_doctor
    span
        font-weight $font-weight-level_6

.doctorPosition
    margin-left 10px

.doctorHonour
    font-size $font-size-level_0
    color white

.honourBack
    background-color $color-theme-blue
    width 100px
    height 20px
    border-radius 3px
    @extend .flex-around

.verbCost
    color $color-theme-gray_1
    font-size $font-size-level_2

.introduce
    @extend .flex
    @extend .flex-row
    justify-content: flex-start
    align-items: flex-start
    background-color white

.verbImg
    width 24px
    margin-top 6px
    margin-left 20px

.introduceLeft
    width 40px

.introduceRight
    flex-grow 2
    pre
        white-space: pre-wrap
        word-wrap: break-word
        margin 10px 25px 0px 25px
        color $color-theme-gray_2
        line-height 17px
        font-size 10px
        overflow hidden

.preHeight
    height 50px

.fold
    @extend .flex
    @extend .flex-column
    @extend .flex-around
    background-color white
    img
        width 22px

.verbDateBoxOut
    overflow-x scroll

.verbDateBox
    @extend .flex
    @extend .flex-row
    @extend .flex-start
    background-color rgb(242, 242, 242)
    height 100px

.verbDate
    @extend .flex
    @extend .flex-column
    @extend .flex-around
    width 50%
    span
        color black
        font-size 13px
        line-height $font-size-level_6
        font-weight $font-weight-level_3
    div
        border-radius 3px
        height $font-size-level_6
        width 70%
        margin-top 3px
        span
            
            line-height $font-size-level_6
            font-size $font-size-level_2

.noVerbDate
    border 1px solid $color-theme-gray_1
    span
        color $color-theme-gray_1

.hasVerbDate
    border 1px solid $color-theme-blue
    span
        color $color-theme-blue

.block
    background-color white
    margin-top 5px
    padding 10px
    pre
        white-space: pre-wrap
        word-wrap: break-word
        text-align left
        margin-top 10px
        padding 10px 25px 40px 25px
        color $color-theme-gray_2
        line-height 20px
        font-size 14px
        color white
        background-color $color-theme-blue

.verbContent
    @extend .flex
    @extend .flex-row
    @extend .flex-start

.verbContentLeft
    flex-grow 2

.verbContentRight
    width 100px
    img
        width 30px

.formItem
  width: 90%
  height: $height_level_form_02
  margin-left: 5%
  margin-top: 20px
  border-bottom 1px solid $color-theme-gray
  display: flex
  @extend .flex-row
  @extend .flex-start
  font-size: $font-size-level_1

.itemInput
  line-height: $height_level_form_02
  background-color: transparent
  border: none
  margin-left: 30px
  flex-grow: 2
  font-size: $font-size-level_1

.itemRadio
  line-height: $height_level_form_02
  background-color: transparent
  border: none
  margin-left: 30px
  font-size: $font-size-level_1

.itemTitle
  width: 60px
  line-height: $height_level_form_02
  font-weight: $font-weight-level_4
  text-align: left
  margin-left: 10px

.verbBox
    position fixed
    height 50px
    width 100%
    bottom 0px
    background-color white
    border-top 1px solid rgba(193, 193, 193, 0.5)
    @extend .flex-around

.verbBtn
    background-color $color-theme-blue
    width 70%
    height 40px
    border-radius 3px
    span
        line-height 40px
        color white
        font-weight $font-weight-level_7
</style>



